export class EstablishRequirementsQueryBean {

    custNo: string                  // 客户号
    custRequestId: string           // 客户请求标识 新增时自动生成
    custRequestTypeId: string       // 请求类型标识 "market-前后台联动 CUST_RIGHTS_COUPON-优惠券 LOAN_REQUEST-申请
    CUST_RIGHTS_COUPON: string      // 优惠券
    custRequestCategoryId: string   // 请求类别标识 "cabinet-前后台联动
    CUST_RIGHTS: string             // 优惠券
    LOAN_REQUEST: string            // 贷款申请
    CREDIT_CARD_REQUEST: string     // 信用卡申请"
    productName: string             // 产品名称 从产品列表选择（产品中心）
    productNo: string               // 产品编号 
    loanAmt: string                 // 贷款金额	 
    statusId: string                // 状态 "CRQ_SUBMITTED：已提交CRQ_ACCEPTED：已接受CRQ_COMPLETED：已完成CRQ_CANCELLED：已取消CRQ_REJECTED：已拒绝CRQ_RETURN：已退回CRQ_INVALIDED：已失效
    CRQ_LANUCH: string              // 待发起"
    pageNum: number                 // 当前页数
    pageSize: number                // 每页条数
}